@import url('https://fonts.googleapis.com/css2?family=Kaushan+Script&family=Montserrat&display=swap');

.App {
  align-items: center;
  display: flex;
  flex-direction: column;
  font-family: Montserrat;
  font-size: 15px;
}

.cart:hover {
	cursor: pointer;
}

.cart:hover + .tooltip {
	display: block;
}

.cart-row {
	align-items: center;
	display: inline-flex;
}

.checkbox {
	height: 15px;
	margin-right: 10px;
	width: 15px;
}

.costume-div {
	align-items: center;
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.curly {
	font-family: "Kaushan Script";
	font-size: 18px;
	margin-left: 5px;
}

.first-words {
	font-family: "Kaushan Script";
	font-size: 20px;
}

.form-div, .generic-div {
	align-items: center;
	background: linear-gradient(133deg, rgba(250,249,177,1) 0%, rgba(255,232,80,1) 68%, rgba(238,170,66,1) 100%);
	border-radius: 10px;
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.full {
	transition: opacity 400ms ease 0ms;
}

.generic-btn {
	background-color: #f5f4cd;
	border: none;
	border-radius: 10px;
	box-shadow: 0 0 10px rgba(238,170,66,1);
	color: black;
	font-family: "Kaushan Script";
	font-size: 16px;
	height: 45px;
	margin: 10px 0 10px 0;
	width: 160px;
}

.generic-btn:hover {
	background-color: #f9f8ec;
	cursor: pointer;
}

.header {
	align-items: center;
	background: radial-gradient(circle, rgba(249,243,246,1) 0%, rgba(192,216,245,1) 100%);
	display: flex;
	justify-content: center;
	position: relative;
	text-align: center;
	width: 100%;
}

.title {
	text-align: center;
}

.tooltip {
	background-color: black;
	border-radius: 10px;
	color: white;
	display: none;
	font-size: 12px;
	height: auto;
	line-height: 1.4;
	margin-top: 570px;
	opacity: 0.7;
	padding: 20px;
	position: absolute;
	text-align: block;
	width: 230px;
}

.tooltip::after {
	content: " ";
  	position: absolute;
  	bottom: 100%;  /* At the top of the tooltip */
  	left: 50%;
  	margin-left: -5px;
  	border-width: 5px;
  	border-style: solid;
  	border-color: transparent transparent black transparent;
}

.wrapper {
	align-items: flex-start;
	display: inline-flex;
	justify-content: flex-end;
	width: 100%;
}

body {
	margin: 0;
}

input, select {
	color: #555;
}

/* Laptops */
@media screen and (min-device-width: 1200px) and (max-device-width: 1600px) {
	.cart {
		border-radius: 5px;
		box-shadow: 6px 6px #73777B;
		height: 140px;
		opacity: 0.8;
		width: auto;
	}

	.cart-div {
		align-items: center;
		display: flex;
		flex-direction: column;
		justify-content: center;
		margin: 0 50px 0 50px;
	}

	.catch-phrase {
		text-align: center;
		padding: 0 10px 0 10px;
	}

	.costume-img {
		box-shadow: 6px 6px #73777B;
		height: 420px;
		width: auto;
	}

	.door-one {
		align-items: center;
		background: radial-gradient(circle, rgba(249,243,246,1) 60%, rgba(192,216,245,1) 100%);
		border-radius: 10px;
		display: flex;
		flex-direction: column;
		height: 580px;
		justify-content: center;
		width: 400px;
	}

	.form-div, .generic-div {
		width: 800px;
	}

	.form-div {
		padding: 10px 60px 10px 60px;
	}

	.form-div-input {
		margin: 10px 0 10px 0;
	}

	.form-div-label {
		padding-right: 20px;
	}

	.form-input, #eye-colors, #skin-tones {
		border: none;
		border-radius: 10px;
		height: 35px;
		width: 100%;
	}

	.form-input-small {
		border: none;
		border-radius: 10px;
		height: 35px;
		width: 45%;
	}

	.generic-btn-row {
		display: flex;
		justify-content: space-between;
		margin: 10px 0 0 0;
		width: 350px;
	}

	.header-title {
		font-family: "Kaushan Script";
		font-size: 50px;
	}

	.intro-div {
		align-items: center;
		height: 580px;
		justify-content: center;
	}

	.intro-p {
		line-height: 1.5;
		width: 80%;
	}

	.main {
		align-items: center;
		display: flex;
		flex-direction: column;
		height: auto;
		justify-content: center;
		margin-top: 30px;
		width: 100%;
	}

	.plate-img {
		height: 90px;
		margin: 0 40px 0 40px;
		width: auto;
	}

	.plate-img-larger {
		border-radius: 50%;
		height: 150px;
		margin: 10px 0 10px 0;
		width: auto;
	}

	.title {
		font-family: "Kaushan Script";
		font-size: 30px;
		margin: 0;
	}
}

